<template>
  <div>
    <button @click="getAllUploadedFiles"></button>
    <!--
      * files  idurl 
      * max  1
      * multiple:  falseBoolean
      * list-type  picture-card()String
        
          * id-card()String
            max11
            multipleexceed-max
            span
          * photo-card()String
      * multipletrueexceed-maxmax
      * add-imgformData
      * remove-imgid
      -->
    <!--
      * 
      * canRemove true
      * cannotRemoveLen 0canRemovefalsefileslength
      * :canRemove="false":cannotRemoveLen="fileLength"
     -->
    <!--
      * 
      * canCrop false
      * autoCrop true
      * autoCropWidth 200
      * autoCropHeight 	200
      * fixed true
      * fixedNumber [1,1]
      * canMoveBox false
      * fixedBox   false
      ***canCropmultipletrue
     -->
     <!--
      * loading
      * onStartLoadingonEndLoading
      * PS:canCroptrue
      -->
    <!--
      * or
      * capture "camera"
      *** PS: capture ***
     -->

    <h2></h2>
    <image-picker
      :files="files"
      :max="maxNum"
      :multiple="true"
      :canCrop="fileCanCrop"
      :canRemove="false"
      :cannotRemoveLen="fileLength"
      @exceed-max="onExceedMax"
      @add-img="onAddImage"
      @remove-img="onRemoveImage"
      @start-loading="onStartLoading"
      @end-loading="onEndLoading" />

    <h2></h2>
    <image-picker
      :canCrop="fileCanCrop1"
      :fixedNumber="[214, 135]"
      list-type="id-card"
      :files="files1"
      capture="camera"
      @add-img="onAddImage1"
      @remove-img="onRemoveImage1">
        <span></span>
      </image-picker>

    <h2></h2>
    <div style="position:relative;">
      <image-picker
        :canCrop="photoCanCrop"
        list-type="photo-card"
        :files="files2"
        @add-img="onAddImage2"
        @start-loading="onStartLoading"
        @end-loading="onEndLoading" />
    </div>
  </div>
</template>

<script>
import Modal from '@/components/modal/'
import Toast from '@/components/toast/'
import ImagePicker from '@/components/imagepicker/ImagePicker'
export default {
  components: {
    ImagePicker
  },
  data () {
    return {
