      fileLength: 0,
      files: [],
      files1: [],
      files2: [],
      fileCanCrop: false,
      fileCanCrop1: true,
      photoCanCrop: true,
      resFn: null
    }
  },
  mounted () {
    this.getFiles()
    this.getFiles1()
    this.getFiles2()
  },
  methods: {
    // 
    getFiles () {
      // 
      this.files = [
        {
          url: 'https://zos.alipayobjects.com/rmsportal/PZUUCKTRIHWiZSY.jpeg',
          id: '2121'
        }
      ]
      this.fileLength = this.files.length
    },
    onAddImage (rst, fnCallback) {
      console.log(rst)
      let formData = new FormData()
      if (this.fileCanCrop) {
        alert('blobData')
        formData.append('file', rst, 'image.png')
      } else {
        formData.append('file', rst.file)
      }
      formData.append('type', 'MEMBER_HEAD')
      // this.files
      // axios.post('', formData, {
        // method: 'post',
        // headers: {
        //   'Content-Type': 'multipart/form-data'
        // })
      // .then(res => {
      //   if (res.data.code === 1) {
      // multiple="true"
      if (this.files.length < this.maxNum) {
        this.files.push({    // push
          url: rst.base64,
          id: 1212
        })
      } else {
        this.onExceedMax(this.maxNum)
      }
      // filevalueinputonchange
      fnCallback()
      //   }
      // })
    },
    onRemoveImage (index) {
      // idthis.files
      this.files.splice(index, 1)    // splice
    },
    // 
    onExceedMax (max) {
      Modal.alert({body: '' + max + ''})
    },
    // 
    getAllUploadedFiles () {
      console.log(this.files)
    },

    // 
    getFiles1 () {
      // 
    },
    onAddImage1 (rst, fnCallback) {
      console.log(rst)
      let formData = new FormData()
      if (this.fileCanCrop1) {
        alert('blobData')
        formData.append('file', rst, 'image.png')
      } else {
        formData.append('file', rst.file)
      }
      formData.append('type', 'MEMBER_HEAD')
      // this.files
      // axios.post('', formData, {
        // method: 'post',
        // headers: {
        //   'Content-Type': 'multipart/form-data'
        // })
      // .then(res => {
      //   if (res.data.code === 1) {
      this.files1 = []
      this.files1.push({    // push
        url: rst.base64,
        id: 1212
      })
      // filevalueinputonchange
      fnCallback()
      //   }
      // })
    },
    onRemoveImage1 (index) {
      // idthis.files
      this.files1.splice(index, 1)    // splice
    },

    // 
    getFiles2 () {
      // 
    },
    onAddImage2 (rst, fnCallback) {
      console.log(rst)
      let formData = new FormData()
      if (this.photoCanCrop) {
        alert('blobData')
        formData.append('file', rst, 'image.png')
      } else {
        formData.append('file', rst.file)
      }
      formData.append('type', 'MEMBER_HEAD')
      // this.files
      // axios.post('', formData, {
        // method: 'post',
        // headers: {
        //   'Content-Type': 'multipart/form-data'
        // })
      // .then(res => {
      //   if (res.data.code === 1) {
      this.files2 = []
      this.files2.push({    // push
        url: rst.base64,
        id: 1212
      })
      // filevalueinputonchange
      fnCallback()
      //   }
      // })
    },

    // loading
    onStartLoading () {
      let promise = new Promise((resolve, reject) => {
        this.resFn = resolve
      })
      Toast.loading('...', promise)
    },
    onEndLoading () {
      this.resFn()
    }
  }
}
</script>



