<template>
  <div>
    <WhiteSpace></WhiteSpace>
    <WingBlank size="lg">
      <h3></h3>
      <r-button size="long" @click="visible = true">Modal </r-button>
      <Modal :visible.sync="visible" :loading.sync="modalLoading" title="title" :bodyStyle="bodyStyle" :onClose="modalConfirmCb">
        <p>...</p>
        <p>...</p>
      </Modal>
    </WingBlank>

    <WhiteSpace></WhiteSpace>
    <WingBlank size="lg">
      <r-button size="long" @click="visibleAlert = true"></r-button>
      <Modal :visible.sync="visibleAlert" :confirmModal="false" title="title" :bodyStyle="bodyStyle" :onClose="modalConfirmCb">
        <p>...</p>
        <p>...</p>
      </Modal>
    </WingBlank>

    <WhiteSpace></WhiteSpace>
    <WingBlank size="lg">
      <h3></h3>
      <r-button size="long" @click="visibleCustom1 = true"></r-button>
      <Modal :visible.sync="visibleCustom1" title="" :bodyStyle="bodyStyle">
      </Modal>

      <WhiteSpace></WhiteSpace>
      <r-button size="long" @click="visibleCustom2 = true"></r-button>
      <Modal :visible.sync="visibleCustom2" :confirmModal="false" title="" :bodyStyle="bodyStyle">
        <div></div>
        <div slot="footer">
          <a href="javascript:;" :class="`${prefixCls}-button`" role="button" @click="multiButtonClickHandler"></a>
          <a href="javascript:;" :class="`${prefixCls}-button`" role="button" @click="multiButtonClickHandler"></a>
          <a href="javascript:;" :class="`${prefixCls}-button`" role="button" @click="multiButtonClickHandler"></a>
        </div>
      </Modal>

      <WhiteSpace></WhiteSpace>
      <r-button size="long" @click="visible = true">Promise</r-button>
      <Modal :visible.sync="visiblePromise" :loading.sync="loadingPromise" title="Promise" :bodyStyle="bodyStyle" :onClose="modalConfirmCb">
        <p>...</p>
        <p>...</p>
      </Modal>
    </WingBlank>

    <WhiteSpace></WhiteSpace>
    <WingBlank size="lg">
      <h3></h3>
      <r-button size="long" @click="alert()">alert</r-button>
    </WingBlank>

    <WhiteSpace></WhiteSpace>
    <WingBlank size="lg">
      <r-button size="long" @click="confirm()">confirm</r-button>
    </WingBlank>
  </div>
</template>

<script>
import { Modal } from '@/components/modal'
import Button from '@/components/button'
import { WhiteSpace, WingBlank } from '@/components/space'
// var Promise = require('es6-promise').Promise
import Promise from 'es6-promise'
import { companyPrefix } from '../config/style.js'
const prefixCls = `${companyPrefix}-modal`
export default {
  components: {
    Modal,
    WhiteSpace,
    WingBlank,
    'r-button': Button
  },
  data() {
    return {
      prefixCls: prefixCls,
      modalLoading: false,
      visible: false,
      bodyStyle: { width: '5.4rem', height: 'auto' },
      visibleAlert: false,
      visibleCustom1: false,
      visibleCustom2: false,
      visiblePromise: false,
      loadingPromise: false
    }
  },
  methods: {
    modalCancelCb() {
      return true
    },
    modalConfirmCb() {
      // let self = this
      return new Promise((resolve, reject) => {
        setTimeout(() => {
          resolve()
        }, 5000)
      })
    },
    multiButtonClickHandler(e) {
      this.visibleCustom2 = false
      Modal.alert({ body: `${e.target.textContent}` })
    },
    alert() {
      Modal.alert({ body: '<b>hahaha</b>' })
    },
    confirm() {
      Modal.confirm({
        title: '',
        okText: '',
        cancelText: '',
        body: '<b>yahaha</b>',
        okCb: function() {
          Modal.alert({ body: '' })
        },
        cancelCb: function() {
          Modal.alert({ body: '' })
        }
      })
    }
  }
}
</script>



