<template>
  <div class="infinite-demo">
    <span v-for="item in tabs" :key="item.id" @click="switchTab(item)" :style="{color:item.id===params?'red':''}">{{item.name}}</span>
    <div class="infinite-wrapper">
      <pull-to-refresh :topLoadMethod="topLoad" :bottomLoadMethod="getData" :loadingConfig="{style: 'circles'}">
        <!--
          slot 
          <div slot="no-result"></div>
          <div slot="no-more"></div>

          prop:
          topLoadMethod 
          bottomLoadMethod 
          loadingConfig {
            type:  --spinner --text  spinner
            style: default    default,bubbles,circles,spiral,waveDots
          }

          topConfig 
          {
            pullText: '',
            // 
            triggerText: '',
            // 
            loadingText: '...',
            // 
            doneText: '',
            // 
            failText: '',
            // 
            loadedStayTime: 400,
            // 
            stayDistance: 50,
            // 
            triggerDistance: 70
          }
          bottomConfig 
          {
            pullText: '',
            // 
            triggerText: '',
            // 
            loadingText: '...',
            // 
            doneText: '',
            // 
            failText: '',
            // 
            'no-moreText': '',
            // 
            'no-resultText': '',
            // 
            loadedStayTime: 400,
            // 
            stayDistance: 50,
            // 
            triggerDistance: 70
          }

          topLoadMethod  bottomLoadMethod   callback load
           load(state)

          state: done  fail  no-more  no-result  reset 
         -->
        <ol>
          <li v-for="(li, index) in list" :key="index">{{li}}</li>
        </ol>
      </pull-to-refresh>

    </div>
  </div>
</template>

<script>
import PullToRefresh from '@/components/pulltorefresh'
export default {
  data() {
    return {
      tabs: [
        { name: 'tab1', id: 0 },
        { name: 'tab2', id: 1 }
      ],
      params: 0,
      offset: 0,
      total: 12,
      list: []
    }
  },
  methods: {
    switchTab(item) {
      this.params = item.id
      this.offset = 0
      this.list = []
      this.pullToLoad('reset')
    },
    getData(load) {
      console.log('loading')
      if (!this.pullToLoad && load) {
        this.pullToLoad = load
      }
      if (this.list.length) {
        this.offset += 6
      }
      setTimeout(() => {
        let str = this.tabs[this.params].name
        for (var i = 1; i < 7; i++) {
          this.list.push(str + '-----------' + (i + this.offset))
        }
        console.log(this.offset)
        if (this.offset === this.total) {
          this.pullToLoad('no-more')
        } else {
          this.pullToLoad('done')
        }
      }, 2000)
    },
    topLoad(load) {
      this.list = []
      this.getData(load)
    }
  },
  components: {
    PullToRefresh
  }
}
</script>
<style scoped>
.infinite-demo {
  text-align: center;
  font-size: 20px;
}
span {
  display: inline-block;
  border: 1px solid #333;
  width: 40vw;
  height: 50px;
  line-height: 50px;
}
.infinite-wrapper {
  height: 60vh;
  overflow-y: auto;
}
li {
  height: 80px;
  line-height: 80px;
  margin-bottom: 20px;
  border: 1px solid #000;
}

li:nth-child(2n) {
  background: orange;
}
</style>



